@import url('https://fonts.googleapis.com/css?family=Noto+Sans&display=swap" rel="stylesheet');
@import url('https://fonts.googleapis.com/css?family=Roboto+Slab:400,700');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400');

body {
  font-family: "Open Sans", serif;
}

.navigation {
  height: 10vh;
  background-color: #4d565f;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  padding-top: 2.5vh;
  font-family: 'Noto Sans', sans-serif;
  font-size: 16px;
}

.wrapper {
  background-color: #f79055;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  height: 75vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  margin: 0 auto;
}

.title-container {
  height: 75vh;
  background: url("img/Weather-Blog-Image.jpg") center center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.title-container__title {
  font-weight: 600;
  font-size: 55px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-weight: 500;
  font-size: 18px;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: 'Noto Sans', sans-serif;
}

.form-container {
  background-color: #2c3e50;
  height: 75vh;
  padding-top: 80px;
}

.about-contact-container {
  background-color: #2c3e50;
  height: 75vh;
  width: 50vw;
  padding: 80px 0 0 80px;
}
.about-contact-container>p {
  color: #fff;
  
}

input {
  height: 37px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f79055;
  width: 30%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

form {
  text-align: center;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f79055;
  color: #fff;
}

button:active {
  outline: none;
}
button:hover {
  background-color: #f8b38b;
}

.weather__info {
  width: 70%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
  margin: 50px 0 0 90px;
  
}

.weather__key {
  color: #f79055;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}